<template>
    <canvas id="hi-analyser"></canvas>
</template>

<script>
export default {
    data() {
        return {
            audioContext: null,
            audioInput: null,
            realAudioInput: null,
            inputPoint: null,
            analyserContext: null,
            canvas: null,
            canvasWidth: null,
            canvasHeight: null,
            canvasHeightHalf: null,
            analyserNode: null,
            zeroGain: null,
            spacing: 100,
        }
    },
    mounted() {
        window.AudioContext = window.AudioContext || window.webkitAudioContext
        this.canvas = document.getElementById("hi-analyser")
    },
    methods: {
        listenStart() {
            if (!this.audioContext) {
                this.audioContext = new AudioContext()
            }
            if (!navigator.getUserMedia) {
                navigator.getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
            }
            if (!navigator.cancelAnimationFrame) {
                navigator.cancelAnimationFrame = navigator.webkitCancelAnimationFrame || navigator.mozCancelAnimationFrame;
            }
            if (!navigator.requestAnimationFrame) {
                navigator.requestAnimationFrame = navigator.webkitRequestAnimationFrame || navigator.mozRequestAnimationFrame;
            }

            navigator.getUserMedia({
                "audio": {
                    "mandatory": {
                        "googEchoCancellation": "false",
                        "googAutoGainControl": "false",
                        "googNoiseSuppression": "false",
                        "googHighpassFilter": "false"
                    },
                    "optional": []
                },
            }, this.gotStream, function() {
                alert('Error getting audio');
                // console.log(e);
            });
        },
        listenStop() {
            if (this.audioContext) {
                this.audioContext.close()
                this.audioContext = null
            }
        },
        updateAnalysers() {
            if (!this.analyserContext) {
                this.canvasWidth = this.canvas.width = ~~(window.innerWidth / 2) * 2;
                this.canvasHeight = this.canvas.height = ~~(window.innerHeight / 2) * 2;
                this.analyserContext = this.canvas.getContext('2d');

                this.canvasHeightHalf = this.canvasHeight / 2;
            }
            window.requestAnimationFrame(this.draw);
        },
        draw()
        // analyzer draw code here
        {
            let heightR = [],
                heightG = [],
                heightB = [],
                numBars = Math.round(this.canvasWidth / this.spacing),
                freqByteData = new Uint8Array(this.analyserNode.frequencyBinCount)

            this.analyserNode.getByteFrequencyData(freqByteData);

            this.analyserContext.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
            let multiplier = this.analyserNode.frequencyBinCount / numBars / 3;

            // Draw rectangle for each frequency bin.
            let pointsR = [{
                x: 0,
                y: this.canvasHeightHalf
            }];
            let pointsG = [{
                x: 0,
                y: this.canvasHeightHalf
            }];
            let pointsB = [{
                x: 0,
                y: this.canvasHeightHalf
            }];

            // let idG = 2,
            //     idB = 1,
            //     rs = 1

            for (let i = 0; i < numBars; ++i) {
                let id = (numBars >> 1) + (i % 2 ? -(i >> 1) : ~~(i / 2) + 1);
                let magnitude = 0;
                let offset = Math.floor(i * multiplier * 3);
                // gotta sum/average the block, or we miss narrow-bandwidth spikes
                for (let j = 0; j < multiplier; j++) {
                    magnitude += freqByteData[offset + j];
                }

                magnitude = magnitude / multiplier / 2.7;
                heightR[i] = ((heightR[i] || (this.canvasHeightHalf - magnitude)) * 3 + this.canvasHeightHalf - magnitude) / 4;

                pointsR[id] = {
                    x: id * this.spacing,
                    y: heightR[i]
                };

                magnitude = 0;
                for (let j = Math.ceil(multiplier); j < multiplier * 2; j++) {
                    magnitude += freqByteData[offset + j];
                }
                magnitude = magnitude / multiplier / 2.3;

                heightG[i] = ((heightG[i] || (this.canvasHeightHalf - magnitude)) * 3 + this.canvasHeightHalf - magnitude) / 4;

                pointsG[id] = {
                    x: id * this.spacing + this.spacing,
                    y: heightG[i],
                };

                magnitude = 0;
                for (let j = Math.ceil(multiplier * 2); j < multiplier * 3; j++) {
                    magnitude += freqByteData[offset + j];
                }
                magnitude = magnitude / multiplier / 2.3;

                heightB[i] = ((heightB[i] || (this.canvasHeightHalf - magnitude)) * 3 + this.canvasHeightHalf - magnitude) / 4;

                pointsB[id] = {
                    y: heightB[i],
                    x: id * this.spacing - this.spacing
                };
            }

            this.analyserContext.lineCap = 'round';
            this.analyserContext.globalCompositeOperation = 'screen';


            this.drawC('rgb(255, 78, 92, 0.9)', pointsR, numBars);
            this.drawC('rgb(47, 255, 169, 0.9)', pointsG, numBars);
            this.drawC('rgb(14, 119, 255, 0.9)', pointsB, numBars);

            window.requestAnimationFrame(this.draw);
        },
        drawC(color, points, numBars) {
            let xc, yc, i

            this.analyserContext.fillStyle = color;
            this.analyserContext.beginPath();
            this.analyserContext.moveTo(points[0].x, points[0].y);

            for (i = 1; i < numBars - 1; i++) {
                xc = (points[i].x + points[i + 1].x) / 2;
                yc = (points[i].y + points[i + 1].y) / 2;
                this.analyserContext.quadraticCurveTo(points[i].x, points[i].y, xc, yc);
            }
            // curve through the last two points
            this.analyserContext.quadraticCurveTo(points[i].x, points[i].y, points[i + 1].x, points[i + 1].y);
            this.analyserContext.closePath();
            this.analyserContext.fill();

            this.analyserContext.beginPath();
            this.analyserContext.moveTo(points[0].x, this.canvasHeight - points[0].y);

            for (i = 1; i < numBars - 1; i++) {
                xc = (points[i].x + points[i + 1].x) / 2;
                yc = this.canvasHeight - (points[i].y + points[i + 1].y) / 2;
                this.analyserContext.quadraticCurveTo(points[i].x, this.canvasHeight - points[i].y, xc, yc);
            }
            // curve through the last two points
            this.analyserContext.quadraticCurveTo(points[i].x, this.canvasHeight - points[i].y, points[i + 1].x, this.canvasHeight - points[i + 1].y);
            this.analyserContext.closePath();
            this.analyserContext.fill();
        },
        gotStream(stream) {
            this.inputPoint = this.audioContext.createGain();

            // Create an AudioNode from the stream.
            this.realAudioInput = this.audioContext.createMediaStreamSource(stream);
            this.audioInput = this.realAudioInput;
            this.audioInput.connect(this.inputPoint);

            //audioInput = convertToMono( input );

            this.analyserNode = this.audioContext.createAnalyser();
            this.analyserNode.fftSize = 1024;
            this.inputPoint.connect(this.analyserNode);

            //audioRecorder = new Recorder(inputPoint);

            this.zeroGain = this.audioContext.createGain();

            this.zeroGain.gain.value = 0.0;
            this.inputPoint.connect(this.zeroGain);
            this.zeroGain.connect(this.audioContext.destination);
            this.updateAnalysers()
        },
    }
}
</script>

<style scoped lang="stylus">
#hi-analyser
    display: block
    background: #00000060
    width: 100%
    height: 100%
</style>
