import Vue from 'vue'
import App from './App.vue'
import Vlf from 'vlf'
import VModal from './components/modal'
import Hiui from './components/hiui'
import router from './router'

Vue.use(Vlf)
Vue.use(VModal, {
    dialog: true
})
Vue.use(Hiui)

Vue.config.productionTip = false

new Vue({
    router,
    render: h => h(App),
}).$mount('#app')
