import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);


let routes = [{
        path: '/',
        component: resolve => require(['@/components/Main.vue'], resolve),
    },
    {
        path: '/todo',
        component: resolve => require(['@/components/apps/todo/Todo.vue'], resolve),
    },
]

export default new Router({
    // mode: 'history',
    routes,
})
